import Fab from '../../components/my-fab.vue'
import LoadMore from '../../components/uni-load-more.vue'
import List from '../../mixins/List.js'
export default {
	name: 'LostInfo',
	mixins: [List],
	components: {
		Fab,
		LoadMore
	},
	data() {
		return {
			info: null,
			isSelf: false,
			avatarPath: '',
			isOpenHelpWindow: false,
			replyContent: '',
			errorInfo: null
		}
	},
	methods: {
		getData () {
			this.$http({
				url: this.$urlPath.getLostInfo,
				params: {
					lid: this.$routeParams.lid
				},
				onRequestSuccess: (res) => {
					this.info = res.data
					this.isSelf = Number(this.$userInfo.state.id) === Number(this.info.uid)
					if (this.info) {
						this.isSelf = Number(this.$userInfo.state.id) === Number(this.info.uid)
						if (this.info.images) {
							this.info.images = this.info.images.split(',')
						}
						this.avatarPath = `background-image: url(${this.$urlPath.imageUrl}${this.info.user_info.avatar});`
						this.page.num = 1
						this.getHelpList()
					}
				},
				onRequestFail: (errorCode, error) => {
					this.$toast(error)
				}
			})
		},
		edit () {
			uni.showActionSheet({
				itemList: ['编辑', '删除'],
				itemColor: '#333333',
				success: (res) => {
					if (res.tapIndex === 0) {
						this.$push('/pages/lost/edit-lost?lid=' + this.$routeParams.lid)
					} else {
						uni.showModal({
							title: '提示',
							content: '是否要删除此求助信息？',
							success: (res) => {
								if (res.confirm) {
									this.closeLost()
								}
							}
						})
					}
				}
			})
		},
		getHelpList () {
			this.$http({
				url: this.$urlPath.getLostHelps,
				params: {
					lid: this.$routeParams.lid,
					page: this.page.num
				},
				onRequestSuccess: (res) => {
					this.loadSuccess(res.data)
				},
				onRequestFail: (code, e) => {
					this.loadFail()
				}
			})
		},
		closeLost () {
			this.$http({
				url: this.$urlPath.closeLost,
				params: {
					lid: this.$routeParams.lid
				},
				onRequestSuccess: (res) => {
					this.$toast('删除成功')
					uni.$emit('refreshLost')
					this.$back()
				},
				onRequestFail: (errorCode, error) => {
					this.$toast(error)
				}
			})
		},
		openHelpWindow () {
			this.isOpenHelpWindow = true
		},
		hidenModal () {
			this.isOpenHelpWindow = false
		},
		replyHelp () {
			if (!this.replyContent) {
				this.errorInfo = '请输入回复内容'
				return
			}
			if (this.replyContent.length < 5) {
				this.errorInfo = '回复内容至少5个字'
				return
			}
			this.$http({
				url: this.$urlPath.addHelpContent,
				params: {
					lid: this.$routeParams.lid,
					content: this.replyContent
				},
				onRequestSuccess: (res) => {
					this.$toast('添加成功，感谢您的爱心帮助')
					this.page.num = 1
					this.getHelpList()
					this.errorInfo = null
					this.isOpenHelpWindow = false
				},
				onRequestFail: (errorCode, error) => {
					this.$toast(error)
				}
			})
		},
		makePhone () {
			uni.makePhoneCall({
				phoneNumber: this.info.concat_phone
			})
		}
	},
	onReachBottom() {
		if (this.loadingType === 'noMore') {
			return
		}
		this.page.num = this.page.num + 1
		this.loadingType = 'loading'
		this.getHelpList()()
	},
	onLoad () {
		uni.$on('refreshLost', () => {
			this.getData()
		})
		this.getData()
	},
	onUnload() {
		uni.$off('refreshLost')
	}
}
